.header{
    display: flex;
    align-items: center;
    padding: 20px;
    justify-content: space-between;
    position: sticky;
    top: 0;
    z-index: 100;
    background-color: white;
}
.header__logo{
    height: 25px;
    object-fit: contain;
    margin-left: 20px;

}
.header__input{
    display: flex;
    align-items: center;
    width: 40%;
    border: 1px solid lightgray;
}
.header__input >input{
    flex: 1;
    border: none;
    padding: 5px;
}
.header__inputButton{
    width: 50px !important;
    background-color: #fafafa;
    border-left: 1px solid lightgray;
    color: gray;
}
.header__input >input:focus{
   outline-width: 0;
    
}
.header__icons{
    display: flex;
    align-items: center;
    
}
.header__icon{
    margin-left: 5px;
}